<template>
  <transition name="slide">
      <div>
          <h4 class="formTitle">{{title}}</h4>
          <div class="formItem">
              <span class="itemTitle">姓名</span>
              <input class="itemInput" placeholder="必填"></input>
          </div>
          <div class="formItem">
              <span class="itemTitle">性别</span>
              <input class="itemRadio" type="radio" name="sex"><label class="itemLabel">男</label></input>
              <input class="itemRadio" type="radio" name="sex"><label class="itemLabel">女</label></input>
          </div>
          <div class="formItem">
              <span class="itemTitle">出生年月</span>
              <input class="itemInput" type="date"></input>
          </div>
          <div class="formItem">
              <span class="itemTitle">手机号码</span>
              <input class="itemInput" placeholder="必填"></input>
          </div>
          <div class="formItem">
              <span class="itemTitle">备注</span>
              <input class="itemInput" placeholder="必填"></input>
          </div>
          <div class="promptDiv">
              <span class="promptText">
                <img src="static/warn.png" class="promptImg">&nbsp;&nbsp;请您填写真实信息，以免影响您就诊
              </span>
          </div>
          <button class="registerBtn" @click="registerClick">立即注册</button>
          <div class="subscript">
            <p>东方医院特需门诊</p>
            <p>服务热线: 5835 6602</p>
          </div>
      </div>
  </transition>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'register',
  data () {
    return {
      title: '个人信息'
    }
  },
  methods: {
    registerClick: function () {
      console.log(123)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
@import "~common/stylus/variable"
@import "~common/stylus/base"

.formTitle
  color: rgb(51, 185, 189)
  height: 40px
  margin-top: 40px
  font-size : $font-size-level_5

.formItem
  background-color: rgba(51, 185, 189, 0.1)
  width: 90%
  height: $height_level_form
  margin-left: 5%
  margin-top: 20px
  border-radius(3px)
  display: flex
  @extend .flex-row
  @extend .flex-start
  font-size: $font-size-level_3

.itemInput
  line-height: $height_level_form
  background-color: transparent
  border: none
  margin-left: 30px
  flex-grow: 2
  font-size: $font-size-level_3

.itemRadio
  line-height: $height_level_form
  background-color: transparent
  border: none
  margin-left: 30px
  font-size: $font-size-level_3

.itemTitle
  width: 60px
  line-height: $height_level_form
  font-weight: $font-weight-level_4
  text-align: left
  margin-left: 10px

.promptImg
  width: 14px

.promptText
  @extend .flex
  @extend .flex-row
  @extend .flex-start
  font-size: 14px
  line-height: 14px

.promptDiv
  margin-top: 24px
  text-align: left
  margin-left: 6%

.itemLabel
  margin-left : 5px

.registerBtn
  background-color :rgb(51, 185, 189)
  border : none
  width : 80%
  height : $height_level_form
  margin-top : 40px
  border-radius(5px)
  color : white
  font-size : $font-size-level_3

.subscript
  font-size : $font-size-level_0
  position: fixed
  bottom: 10px
  left: 0px
  right: 0px
  color: $color-theme-gray
</style>



